<template>
  <div class="cart-bundle__line-item-wrapper">
    <a
      class="ajax-cart__image responsive-image__wrapper knockout-image"
      :href="`/products/${bundle.handle}`"
    >
      <img
        :alt="getVariant(bundle).title"
        sizes="auto"
        :src="getImageUrl(getVariant(bundle).featured_image.src, '50x')"
        :srcset="`
          ${getImageUrl(getVariant(bundle).featured_image.src, '120x')} 120w,
          ${getImageUrl(getVariant(bundle).featured_image.src, '240x')} 240w,
          ${getImageUrl(getVariant(bundle).featured_image.src, '320x')} 320w
        `"
      >
    </a>

    <div class="cart-bundle__description">
      <div class="cart-bundle__inner">
        <a
          class="cart-bundle__title subtitle-2"
          :href="`/products/${bundle.handle}`"
          v-text="bundle.title"
        />
        <div class="cart-bundle__variant-title">
          <span
            v-for="(option, index) in bundle.options"
            :key="`bundle-options-${index}`"
          >
            <template v-if="option.toLowerCase() !== 'title'">
              {{ option }}: {{ getVariant(bundle).options[index] }}
            </template>
          </span>
        </div>
        <div class="ajax-cart__price body-2">
          <span
            class="visually-hidden"
            v-text="$string('cart.label.original_price')"
          />
          <s
            v-text="$formatMoney(strikeOutPrice)"
          />

          <span
            class="visually-hidden"
            v-text="$string('cart.label.discounted_price')"
          />
          <span
            class="ajax-cart__price-current"
            v-text="$formatMoney(getDiscountPrice(bundle))"
          />
          <span
            class="badge badge--primary"
            v-text="discountText"
          />
        </div>

        <div class="cart-bundle__swatches">
          <template
            v-for="variant in bundle.variants"
            :key="variant.sku"
          >
            <button
              v-if="variant.available"
              class="product-card-swatches__swatch"
              :class="[
                `product-card-swatches__swatch--${hasColorSwatch(bundle) ? 'color' : 'size'}`,
                {'is-active': selectedVariant === variant.id}
              ]"
              :title="variant.public_title"
              type="button"
              @click="selectedVariant = variant.id"
            >
              <img
                v-if="hasColorSwatch(bundle)"
                :alt="variant.public_title"
                class="product-card-swatches__image"
                :src="swatchUrl(bundle, variant)"
              >

              <span
                v-else
                v-text="variant.public_title"
              />
            </button>
          </template>
        </div>

        <button
          class="cart-bundle__add"
          tabindex="0"
          :data-cart-add="getVariant(bundle).id"
          @click.prevent="handleAddToCart(bundle)"
        >
          <span v-text="$string('product.cta.add_to_bag')" />
          <span
            class="is-hidden"
            v-text="$string('product.cta.add_to_cart_limit')"
          />
        </button>
      </div>
    </div>
  </div>
</template>

<script>

/**
 * Vue: Cart Bundle Slide (cart-bundle-slide)
 * -----------------------------------------------------------------------------
 * Newsletter form.
 *
 * @param {String} newsletterErrors - Errors from Shopify form
 * @param {Boolean} requiresConsent - Check if consent checkbox is required
 * @param {String} shopifyFormId - Shopify form ID
 *
 */

import { getSizedImageUrl } from '~/helpers/format'
import { mapActions } from 'vuex'
import timings from '~/config/timings'

export default {
  name: 'CartBundleSlide',

  props: {
    bundle: {
      type: Object,
      default: () => ({}),
    },
  },

  data() {
    return {
      selectedVariant: this.bundle.variants[0]?.id || null,
    }
  },

  computed:{
    discountText() {
      if (this.bundle.discountType === 'percentage') {
        return this.getPercentText(this.bundle.discountAmount)
      } else if (this.bundle.discountType === 'amount') {
        return this.getPercentTextNew(this.$formatMoney(this.bundle.discountAmountSaved * 100))
      }
      return ''
    },

    strikeOutPrice() {
      if (this.getVariant(this.bundle).compare_at_price === null) {
        return this.getVariant(this.bundle).price
      }
      else {
        return this.getVariant(this.bundle).compare_at_price
      }
    },
  },

  methods: {

    /**
     * Map Vuex actions.
     */
    ...mapActions({
      addItem: 'cart/addItem',
    }),

    /**
     * Handle add to cart submission.
     */
    async handleAddToCart(item) {
      const variant = this.getVariant(item)
      if (!item || !variant) {
        return
      }

      clearTimeout(this.timeout)
      this.error = ''
      this.state = 'adding'

      if (cnvs.storybook) {
        this.timeout = setTimeout(() => {
          this.state = 'added'
        }, timings.slow)

        this.timeout = setTimeout(() => {
          this.state = 'ready'
        }, timings.message)

        return
      }


      const lineItemProperties = {}
      lineItemProperties._bundleAddOn = true
      const items = [
        {
          id: variant.id,
          properties: lineItemProperties,
          quantity: 1,
        },
      ]

      try {
        await this.addItem(items)

        this.state = 'added'

        this.timeout = setTimeout(() => {
          this.state = 'ready'
          this.$emit('check-cart-bundle')
          this.$emit('collapse-accordion')
        }, timings.message)

      } catch (error) {
        this.state = 'error'

        if (error.length) {
          this.error = error
          return
        }

        reportError('Failed to add product to cart', error)
      }
    },

    /**
     * Get percentage discount text.
     * @param {Object} item - Current line item
     * @returns {Number}
     */
    getDiscountPrice(item) {
      const variant = this.getVariant(item)
      let compareAtPrice

      if (variant.compare_at_price === null) {
        compareAtPrice = variant.price
      } else {
        compareAtPrice = variant.compare_at_price
      }

      if (item.discountType === 'percentage') {
        const discountPercentage = item.discountAmount / 100
        const discountAmount = compareAtPrice * discountPercentage
        return Math.round(compareAtPrice - discountAmount)
      }
      else
      {
        return compareAtPrice - (item.discountAmountSaved * 100)
      }
    },

    /**
     * Get percentage discount text.
     * @param {String} discount - Item percent discount
     * @returns {String}
     */
    getPercentText(discount) {
      return this.$string('product_bundles.percent_off').replace('{{amount}}', discount)
    },
    getPercentTextNew(discount) {
      return this.$string('product_bundles.amount_off').replace('{{amount}}', discount)
    },


    /**
     * Get one variant from product bundle.
     * @param {Object} item - One product from bundle
     * @returns {String}
     */
    getVariant(item) {
      let variantAvailable = item.variants.find((variant) => {
        return variant.id === this.selectedVariant && variant.available === true
      })

      if (!variantAvailable) {
        variantAvailable = item.variants[0]
        this.selectedVariant = variantAvailable.id
      }

      return variantAvailable
    },

    getImageUrl(src, size) {
      return getSizedImageUrl(src, size)
    },

    swatchUrl(item, variant) {
      const color = variant.options[0].toLowerCase()
      const swatchUrl = item.swatchUrls[color]

      return swatchUrl
    },

    hasColorSwatch(item) {
      return item.options[0].toLowerCase() === 'color'
    },
  },
}
</script>